@use "variables" as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$blanco;
    color: v.$negro;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
label {
    font-size: 2rem;
}

.descripcion-pagina {
    margin: 2rem 0;
    font-family: v.$fuente_secundaria;
}

.home {
    display: flex;
    margin-top: 2rem;
    img {
        width: 28px;
        height: 28px;
    }
}
